@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* .tui-ico-ellipsis, .tui-ico-first, .tui-ico-last, .tui-ico-next, .tui-ico-prev {
    overflow: hidden;
} */

.tui-pagination {
  // height: 40px;

  margin-bottom: 40px;
  line-height: normal;
  text-align: center;
  font-size: 0;

  @media screen and (min-width: $tabletBreakpoint) {
    margin-bottom: 60px;
  }
}

.tui-pagination .tui-page-btn {
  display: inline-flex;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 0;
  margin-left: 0;
  color: $primary-text-color;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  /* border: 1px solid #ddd; */
  /* border-width: 1px 0; */
}

.tui-pagination .tui-page-btn:hover {
  background-color: $bg-second-color;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: $white-color;
  background: $color-accent;
  border-color: $color-accent;
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: $color-accent;
}

